// ** React Imports
import { Fragment, useState, useEffect } from 'react'
// import Swal from 'sweetalert2'
// import withReactContent from 'sweetalert2-react-content'
// ** Invoice List Sidebar
// ** Columns
// import { columns } from './columns'
// import AddQuiz from '../add'

// ** Store & Actions
import { getAllWallData, editPostData, Feedtogglestatus } from '../store/action' //deleteFeedPost
import { useSelector, useDispatch } from 'react-redux'
// import { selectThemeColors } from '@utils'

// ** Third Party Components
import ReactPaginate from 'react-paginate'
import { ChevronDown, MessageCircle, Heart, Edit, MoreVertical, Filter } from 'react-feather' //MoreVertical Trash2
import DataTable from 'react-data-table-component'
import UiLoader from '@components/ui-loader'
import { Card, Col, CustomInput, DropdownToggle, DropdownMenu, DropdownItem, UncontrolledDropdown, CardHeader, Label, Row, Input
} from 'reactstrap' //DropdownToggle, DropdownMenu, DropdownItem, UncontrolledDropdown 

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import { useHistory } from 'react-router-dom'
import AppCollapse from '@components/app-collapse'

// ** Table Header
// const CustomHeader = ({ toggleSidebar, handlePerPage, rowsPerPage }) => {
// const CustomHeader = ({ handlePerPage, rowsPerPage }) => {
  const CustomHeader = () => {

}

const UsersList = () => {
  // ** Store Vars
  const dispatch = useDispatch()
  const store = useSelector((state) => state.feeds)
  const history = useHistory()
  // ** States
  const [searchTerm, setSearchTerm] = useState('')
  const [listData, setListData] = useState([])
  const [currentPage, setCurrentPage] = useState(1)
  const [states, setStates] = useState(false)
  const [dataPerPage, setDataPerPage] = useState('')
  const [searchName, setSearchName] = useState('')
 // const [scrollModal, setScrollModal] = useState(false)
 
  const [currentRole] = useState({
    value: '',
    label: 'Select Role'
  })
  const [currentPlan] = useState({
    value: '',
    label: 'Select Plan'
  })
  const [currentStatus] = useState({
    value: '',
    label: 'Select Status',
    number: 0
  })

  // ** Function to toggle sidebar
  const toggleSidebar = () => history.push('/walls_add')
  //const MySwal = withReactContent(Swal)
  useEffect(() => {
    dispatch(getAllWallData(currentPage, dataPerPage))
    // setListData(dummyData)
  }, [currentPage, store.toggle_data])

  useEffect(() => {
    dispatch(getAllWallData(1, dataPerPage))
  }, [dataPerPage])

  useEffect(() => {
    if (store.feedAllData) {
      setListData(store.feedAllData)
    }
  }, [JSON.stringify(store.feedAllData)])
//const err = store.delete ? "erortrue" : "errorfalse"
//console.log(err)
console.log(store)


  // const handleConfirmText = (id) => {
  //   return MySwal.fire({
  //     title: 'Are you sure?',
  //     text: "You won't be able to revert this!",
  //     icon: 'warning',
  //     showCancelButton: true,
  //     confirmButtonText: 'Yes, delete it!',
  //     customClass: {
  //       confirmButton: 'btn btn-primary',
  //       cancelButton: 'btn btn-outline-danger ml-1'
  //     },
  //     buttonsStyling: false
  //   }).then(function (result) {
      
  //    if (result.value) {
  //       dispatch(deleteFeedPost(id))
  //       MySwal.fire({
  //         icon: 'success',
  //         title: 'Deleted!',
  //         text: 'Your file has been deleted.',
  //         customClass: {
  //           confirmButton: 'btn btn-success'
  //         }
  //       })
  //     }
  //   })
  // }

  
  const   editHandler = (row) => {
   dispatch(editPostData(row))
    history.push(`/walls/edit/${row.feed_id}`)
  }
  const SwitchClick = (row) => {
    
    dispatch(Feedtogglestatus(row, states))
    setStates(!states)
  }

  const formatDate = (value, formatting = { month:'short', day:'numeric', year:'numeric', hour:'numeric', minute:'numeric', second:'numeric' }) => {
    if (!value) return value
    return new Intl.DateTimeFormat('en-GB', formatting).format(new Date(value))
  }
console.log(store)
  const columns = [
    // export const columns = () => [
      {
        name: 'Sno',
        minWidth: '10px',
        selector: 's_no',
        cell: (row, pos) => `#${store.wall_page.from + pos}`
      }, 
      {
        name: 'Image',
        minWidth: '200px',
        selector: 'feed_image',
        cell: (row) => (
          <img src={row.feed_image} style={{ height: '50px', width: '150px'}} />
        )
      },
      {
        name: 'Type',
        minWidth: '160px',
        selector: 'feed_type',
        sortable: true,
        cell: (row) => row.feed_type
      },
      {
        name: 'Engagement',
        minWidth: '160px',
        selector: 'feed_comments',
        sortable: true,
        cell: (row) => (
                      <div
                      // color='flat-primary'
                      style={{cursor : 'pointer'}}
            className='user-name text-truncate mb-0'
            // onClick={() => setScrollModal(!scrollModal)}
          >
          <div className="d-flex">
            <div className="pr-2"><MessageCircle width="14" /> {row.feed_comments}</div>
            <div><Heart width="14" /> {row.feed_likes}</div>        
          </div>
            </div>
        )
      },
      {
        name: 'Date & Time',
        minWidth: '200px',
        selector:'updated_on',
        sortable: true,
        cell: (row) => formatDate(row.updated_on)
      
      },
      {
        name: 'Status',
        minWidth: '138px',
        selector: 'feed_id',
        sortable: true,
        cell: (row) => (
          <div className="column-action">
            {
             
              (
                <CustomInput
              className='custom-control-success'
              type='switch'
              id={`success${row.feed_id}`}
              name='success'
              onChange={() => SwitchClick(row.feed_id)}
              inline
              defaultChecked = {row.status !== 'DL'}
            />
              )
            }
           
          
          </div>
        )
      },
      {
        name: 'Actionss',
        minWidth: '138px',
        selector: 'active',
        cell: (row) => (
          <UncontrolledDropdown>
          <DropdownToggle className='pr-1' tag='span'>
            <MoreVertical size={15} />
          </DropdownToggle>
          <DropdownMenu right>
            <DropdownItem tag='a'  className='w-100' onClick={() => editHandler(row)} >
              <Edit size={15}/>
              <span className='align-middle ml-50'>Edit</span>
            </DropdownItem>
            {/* <DropdownItem tag='a' href='/' className='w-100' onClick={e => e.preventDefault()}>
              <Archive size={15} />
              <span className='align-middle ml-50'>Archive</span>
            </DropdownItem> */}
            {/* <DropdownItem tag='a' className='w-100' onClick={() => handleConfirmText(row.feed_id)}>
              <Trash2 size={15} />
              <span className='align-middle ml-50'>Delete</span>
            </DropdownItem> */}
          </DropdownMenu>
        </UncontrolledDropdown>
          // <div className="column-action">
          //   {/* <a href={`/walls/edit/${row.feed_id}`}> */}
          //   <Edit width="17" className="cursor-pointer" title="Edit Feed" onClick={() => editHandler(row)} />
          //   {/* </a> */}
          //   <Trash2 width="17" className="ml-2 cursor-pointer" title="Add Feed" onClick={() => handleConfirmText(row.feed_id)} />
          // </div>
        )
      }
     
    ]


  // ** User filter options

  // ** Function in get data on page change
  const handlePagination = (page) => {
    setCurrentPage(page.selected + 1)
  }

  // ** Function in get data on rows per page
  // const handlePerPage = (e) => {
  //   const value = parseInt(e.currentTarget.value)
  //   setRowsPerPage(value)
  //   setCurrentPage(1)
  // }

  // ** Function in get data on search query change
  const handleFilter = (val) => {
    setSearchTerm(val)
  }

  if (store.loadFeedAll) {
    return (
      <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
        <UiLoader blocking />
      </div>
    )
  }
console.log(store.feedAllData)
console.log(store.wall_page)
  // ** Custom Pagination
  const CustomPagination = () => {
    // const count = Number(
    //   Math.ceil(store.feedAllData.record_count / rowsPerPage)
    // )
    const count = store.wall_page.last_page
    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={
          'pagination react-paginate justify-content-end my-2 pr-1'
        }
      />
    )
  }
  
  const handleNameFilter = (value) => {
    setCurrentPage(1)
    setSearchName(value)
  }
  
  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      role: currentRole.value,
      currentPlan: currentPlan.value,
      status: currentStatus.value,
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    if (listData.length === 0 && isFiltered) {
      return []
    } else {
      return listData
    }
  }
  console.log(searchName)

  const data = [
    {
    title:<div><Filter size={18} /> Filter</div>,
    content:(
      <>
      <Row xl={12}>
        <Col
          xl="12"
          className="d-flex align-items-sm-center justify-content  flex-lg-nowrap flex-wrap flex-sm-row flex-column pr-lg-1 p-0 mt-lg-0 mt-1"
        >
          <div className="align-items-center mb-sm-0 mb-1 mr-1 ml-1">
            <Label>
              Search:
            </Label>
            <Input
              id="search-invoice"
              className="w-100"
              type="text"
              value={searchName}
              placeholder='search'
              onChange={(e) => handleNameFilter(e.target.value)}
              // onKeyDown={(e) => handleKeyPress(e)}
            />
          </div>
       </Col>
       </Row>
      </>
    )  
    }
  ]

  return (
    <Fragment>
      <AppCollapse data={data} type='border' accordion className='mb-1' />
      <Card>
        <CardHeader>
        <Col xl="12" className="d-flex align-items-center p-0">
          <div className="d-flex align-items-center w-100">
            <Label for="rows-per-page">Show</Label>
            <CustomInput
              className="form-control mx-50"
              type="select"
              id="rows-per-page" 
              value={dataPerPage}
              onChange={(e) => setDataPerPage(e.target.value)}
              style={{
                width: '5rem',
                padding: '0 0.8rem',
                backgroundPosition:
                  'calc(100% - 3px) 11px, calc(100% - 20px) 13px, 100% 0'
              }}
            >
              <option value="10">10</option>
              <option value="50">50</option>
              <option value="100">100</option>
            </CustomInput>
            <Label for="rows-per-page">Entries</Label>
          </div>
        </Col>
        </CardHeader>
        <DataTable
          noHeader
          // pagination
          responsive
          paginationServer
          columns={columns}
          sortIcon={<ChevronDown />}
          className="react-dataTable"
          paginationPerPage
          paginationDefaultPage={currentPage}
          paginationComponent={CustomPagination}
          data={dataToRender()}
          subHeaderComponent={
            <CustomHeader
              toggleSidebar={toggleSidebar}
              // handlePerPage={handlePerPage}
              rowsPerPage
              searchTerm={searchTerm}
              handleFilter={handleFilter}
            />
          }
        />
      </Card>
      {
        listData.length === 0 ? '' : <CustomPagination />
      }
    </Fragment>
  )
}

export default UsersList
